import axios from 'axios'

/**
 * axios
 * @param {object} config axios
 * @return {object} promise error
 **/
let baseUrl=window.location.href.match(/^http.+(?=\/phwechat)/g)[0];
axios.interceptors.request.use(config => {
  return config
}, error => {
  return Promise.reject(error)
})

/**
 * axios 
 * @param {object} response error
 * @return {object} promise error
 **/
axios.interceptors.response.use(response => {
  return response
}, error => {
  return Promise.resolve(error)
})

/**
 * ,response,
 * @param {object} response 
 * @return {object} 
 **/
function checkStatus (response) {
  // ,htttp400500
  if (response && ((response.status === 200 || response.status === 304 || response.status === 400))) {
    return response.data // http responsedata,datacode,message,data
  }
  return { // 
    code: '404',
    message: ''
  }
}

/**
 * 
 * @param {object} res http 
 * @return {object} code,message,data
 **/
// function checkCode (res) {
//   // 
//   console.log(res)
//   if (res.code === -404) { // 
//     alert(res.message)
//     return {code: '', message: ''}
//   } else { // codecode
//     return res
//   }
// }

export default {
  post (url, data) {


    return axios({
      method: 'post',
      baseURL: process.env.NODE_ENV==='development'? '':baseUrl,
      url: url,
      transformRequest: [function (data) {
        //  data 
        let formUrl='';
        for( let k in data){
          if(data.hasOwnProperty(k)){
           formUrl+=`${k}=${data[k]}&`
          }
        }
        formUrl=formUrl.substring(0,formUrl.length-1);
        return formUrl;
      }],
      data: data,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      },
      timeout: 120000
    }).then((res) => {
      console.log(res);
      return checkStatus(res)
    })
  },
  get (url, params) {
    return axios({
      method: 'get',
      baseURL: process.env.NODE_ENV==='development'? '':baseUrl,
      url,
      params,
      timeout: 120000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest'
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    )
  }

}



