          NO02_ENOUGHREST: '',
          NO03_SLEEPHOUR: '',
          NO04_CANNOTSLEEP: '',
          NO05_EASILYAWAKENED: '',
          NO06_FALLASLEEP: '',
          NO07_WAKESLEEP: '',
          NO08_EARLYAWAKENING: '',
          NO09_WAKINGTIME: '',
          NO10_DISCOMFORT: '',
