          NO16_EASYANGRY: '',
          NO17_LOWSPIRITS: '',
          NO18_THINKWORRY: '',
          NO19_MISSTHISANDTHAT: '',
          NO20_AFFECTAPPETITE: ''

        },
        TCMSLEEP_SCORE: 0,
        TCMSLEEP_RESULT: 0,
        TCMSLEEP_PROPOSAL: 0,
        canNext: false,
        canSubmit: false,
        curNum: 1,
        total: 0,
        runing: false,
        sex:'',
        age:''
      }
    },
    created() {
      this.copyData = [
        {
          name: 'NO02_ENOUGHREST',
          quesTitle: '2. ',
          answers: ['', '']
        },
        {
          name: 'NO03_SLEEPHOUR',
          quesTitle: '3. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO04_CANNOTSLEEP',
          quesTitle: '4. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO05_EASILYAWAKENED',
          quesTitle: '5. ',
          answers: ['', '', '', '']
        },
        {
          quesTitle: '6. ',
          subQues: [
            {
              name: 'NO06_FALLASLEEP',
              subQuesTitle: '',
              subAnswers: ['', '', '', '', '']
            },
            {
              name: 'NO07_WAKESLEEP',
