          answers: ['', '', '', '']
        },
        {
          name: 'NO10_DISCOMFORT',
          quesTitle: '9. ',
          answers: ['3', '', '3']
        },
        {
          name: 'NO11_DIZZY',
          quesTitle: '10. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO12_DULLEYEPAIN',
          quesTitle: '11. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO13_FLUSTERED',
          quesTitle: '12. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO14_PIBPAIN',
          quesTitle: '13. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO15_STILLFATIGUE',
          quesTitle: '14. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO16_EASYANGRY',
          quesTitle: '15. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO17_LOWSPIRITS',
          quesTitle: '16. ',
          answers: ['', '', '', '']
        },
        {
          name: 'NO18_THINKWORRY',
          quesTitle: '17. ',
