  }

  .item .flex .my-radio:first-child {
    margin-top: 0;
  }

   .question {
    font-size: 36px; /*px*/
    color: #18b7f1;
    margin-bottom: 24px;

  }

  .test-list {
    width: 697px;
    margin: 0 auto;
    position: relative;

    & .pli {

    }
    & .item {
      background-color: #e7f8fe;
      margin-top: 5px;
      border-radius: 5px;
      padding: 55px 45px;

    }
    & .item:first-child {
      margin-top: 0;
    }
  }

  .answer-option {

  }

  .submit {
    width: 500px;
    height: 57px;
    line-height: 34px;
    font-size: 36px; /*px*/
    color: #fff;
    background-color: #18b7f1;
    border-radius: 5px;
    -webkit-appearance: none;
  }

  .btn {
    margin-top: 40px;
    text-align: center;
    padding-bottom: 100px;
  }

  #plist {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: $headerHeight;
  }
</style>



