<template>
  <div>
    <header class="header flex direction-column justify-center">
      <p>{{title}}</p>
      <p><b>{{titleResult}}</b></p>
    </header>
    <div id="rador" v-if="resultText!==''">
      <chart :options="polar" v-if="showChart"></chart>

    </div>
    <div class="chart-result" v-if="resultText!==''">
      {{resultText}}
    </div>
    <div class="suggests">
      <div class="sug-item">
        <h2 class="sug-title"></h2>
        <section>
          <p class="txt">{{suggests}}
          </p>
        </section>
      </div>
      <div class="sug-item">
        <h2 class="sug-title"></h2>
        <section>
          <div v-if="isArray(improve)&& improve.length>0">
            <div>
              <div class="per per-ti">
                
              </div>
              <ul class="me-lst">
                <li class="me-lst-li" v-for="(item,index) in improve" :key="index">
                  <div class="bar bar-out"><span class="bar bar-in" :style="{width:item.percent}"></span></div>
                  <p>{{item.name}} <span class="per">{{item.percent}}</span></p>
                </li>

              </ul>
            </div>
          </div>
          <div v-else class="syn-improve">
            {{improve}}
          </div>
          <p class="user-num">
            <span>5577</span><br>
            
          </p>
        </section>
      </div>
    </div>
    <div class="code flex justify-center align-center">
      <img :src="$STATIC+'static/result/code.png'">
    </div>
    <footer class="footer">
      <div class="re-test">
        <span @click="reTest">&gt;&gt;</span>
      </div>
      <div class="isHelp">
        <span>{{result}}</span>
        <input type="button" value="" @click="setHelp($event)" :class="{iptActive:isHelp==='1'}">
        <input type="button" value="" @click="setHelp($event)" :class="{iptActive:isHelp==='0'}">
      </div>
    </footer>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import {isArray} from '@/common/utils'

  export default {
    name: 'result',
    data() {
      return {
        fromPage: '',// 
        titleResult: '',// 
        isHelp: '',
        score: '',//
        showChart: true,// echarts
        polar: {
          radar: [
            {
              name: {
                show: true,
                color: '#666',
                fontSize: '100%'
              },
              splitNumber: 4,//
              scale: false,
              splitLine: { //
                lineStyle: {
                 width:0
                }
              },
              indicator: [//
                {text: '', max: 100},
                {text: '', max: 100},
                {text: '', max: 100},
                {text: '', max: 100}
              ],
              splitArea: {//
                show: true,
                areaStyle: {
                  color:['#b1e6ff','#b1e6ff','#4bc6ff','#0094d9']
                }
              },
              center: ['50%', '50%'],
              radius: '65%',//
              axisLabel: {//
                show: false,
                showMaxLabel: true,
                fontSize: '80%',
                width: 1,
                color:'#fff'
              },
            }
          ],
          series: [
            {
              name: '',
              type: 'radar',
              radarIndex: 0,
              data: [
                {
                  value: [50, 50, 50, 50],
                  name: '',
                  label: {
                    normal: {
                      show: false,
                      formatter: function (params) {
                        return params.value;
                      }
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: '#b1e6ff',
                      borderWidth: 5,
                      opacity: 0.8
                    }
                  },//
                  lineStyle: {
                    normal: {
                      width: 1.5,
                      color: '#fff',
                    }
                  },//
                  areaStyle: {// 
                    normal: {
                      color: '#fff',
                      opacity: 0.5
                    }
                  }
                }
              ]
            }
          ]
        },// echarts
        fromPath: '',// 
        suggests: '', // 
        improve: [],//
        resultText: '',// 
      }
    },
    created() {
      console.log('created')
      if (this.$route.query.showChart === false) {//echarts
        this.showChart = false;
      }
      console.log(this.$route.query);
      this.fromPage = this.$route.query.from;// 
      this.score = this.$route.query.score==='undefined'?undefined:this.$route.query.score+"";// 
      this.setTopTitle();
      this.setSuggest();//
      this.setImprove();
      this.setResultText();
    },
    methods: {
      setHelp(e) {
        let dom = e.target;
        dom.value === '' ? this.isHelp = '1' : this.isHelp = '0';
      },
      reTest() {
        this.$router.push({path:`/${this.fromPage}`,query:{allow:true} });
      },
      setSuggest() {
        let from = this.fromPage;
        switch (from) {
          case 'emotion':
            this.suggests = `
              
              
              
              
              
              2
              
              `;
            break;
          case 'tcmsleep':
            this.suggests = `
              
              `;
            break;
          case 'syndrome':
            let data={
              SYNDROME_RESULT:this.$route.query.SYNDROME_RESULT
            }
            this.$http.post('/phwechat/zhenghoujieguo.do',data).then((res)=>{
              this.suggests = res.data_02;
              this.improve=res.data_01;
            });

            break;
          default:
            break;
        }
      },// 
      setImprove() {
        let list = [
          {
            name: '',
            percent: '70%'
          },
          {
            name: '',
            percent: '68%'
          },
          {
            name: '',
            percent: '68%'
          },
          {
            name: '',
            percent: '67%'
          },
          {
            name: '',
            percent: '62%'
          }
        ]
        if (this.fromPage === 'emotion') {
          list = [
            {
              name: '',
              percent: '80%'
            },
            {
              name: '',
              percent: '68%'
            },
            {
              name: '',
              percent: '53%'
            },
            {
              name: '',
              percent: '67%'
            },
            {
              name: '',
              percent: '62%'
            },
          ]
        } else if (this.fromPage === 'syndrome') {
          list = [];
        }
        this.improve = list;
      },//
      setTopTitle() {

        let deep = '';// 
        let deepNum;// 
        if (this.score !== undefined && this.fromPage !== undefined) {
          deepNum = this.score;
          if (this.fromPage === 'emotion') {
            switch (deepNum) {
              case '0':
                deep = '';
                this.polar.series[0].data[0].value[0] = 100;
                break;
              case '1':
                deep = '';
                this.polar.series[0].data[0].value[3] = 100;
                break;
              case '2':
                deep = '';
                this.polar.series[0].data[0].value[2] = 100;
                break;
              case '3':
                this.polar.series[0].data[0].value[1] = 100;
                deep = '';

                break;
              default:
                break;

            }
          }
          else if (this.fromPage === 'tcmsleep') {
            this.polar.radar[0].indicator = this.polar.radar[0].indicator.map((item, index) => {
              let o = {text: '', max: 100}
              switch (index) {
                case 0:
                  o.text = '';
                  break;
                case 1:
                  o.text = '';
                  break;
                case 2:
                  o.text = '';
                  break;
                case 3:
                  o.text = '';
                  break;
                default:
                  break;
              }
              return o;
            });
            deep = `${deepNum}`;
            if (deepNum < 2) {
              this.polar.series[0].data[0].value[0] = 100;
              this.polar.radar[0].indicator[0].text = '';
            }
            else if (deepNum >= 2 && deepNum <= 12) {

              this.polar.series[0].data[0].value[3] = 100;
              this.polar.radar[0].indicator[3].text = '';
            } else if (deepNum >= 13 && deepNum <= 36) {
              this.polar.radar[0].indicator[2].text = '';
              this.polar.series[0].data[0].value[2] = 100;
            } else {
              this.polar.radar[0].indicator[1].text = '';
              this.polar.series[0].data[0].value[1] = 100;
            }
          }
          else if(this.fromPage==='syndrome'){
            deep=this.$route.query.SYNDROME_RESULT.slice(0,this.$route.query.SYNDROME_RESULT.length-1)
//            this.$http.post('/api/zhenghoujieguo').then((res)=>{
//              console.log(res);
//            })
          }
          this.titleResult = deep;//
        }
        else{ //
          this.titleResult=this.$route.query.SYNDROME_RESULT.slice(0,this.$route.query.SYNDROME_RESULT.length-1);
        }
        if (this.fromPage !== undefined) {//

          switch (this.fromPage) {
            case 'emotion':
              this.title = '';
              break;
            case 'tcmsleep':
              this.title = '';
              break;
            case 'syndrome':
              this.title = '' +
                '';
              break;
            default:
              break;
          }
        }
      },// ,
      setResultText() {// 

        if (this.fromPage === 'emotion') {
          switch(this.score){
            case '0':
              this.resultText='';
              break;
            case '1':
              this.resultText='';
              break;
            case '2':
              this.resultText='';
              break;
            case '3':
              this.resultText='';
              break;
          }
        }
        else if(this.fromPage==='tcmsleep'){

          if (this.score < 2) {
            this.resultText='';
          }
          else if (this.score >= 2 && this.score <= 12) {
            this.resultText='';
          } else if (this.score >= 13 && this.score <= 36) {
            this.resultText='';
          } else {
            this.resultText='';
          }
        }
        else if(this.fromPage==='syndrome'){
          this.resultText='';
        }
      },
      isArray(a){
        return isArray(a);
      }
    },
    computed: {
      ...mapState(['result'])
    },
    beforeRouteEnter(to, from, next) {
      console.log('',to.fullPath,from.fullPath)
      next();
      //  confirm 
      //  `this`
      // 
    },
    beforeRouteUpdate(to, from, next) {
      console.log('beforeRouteUpdate')
      next();
      // 
      //  /foo/:id /foo/1  /foo/2 
      //  Foo 
      //  `this`
    },
    beforeRouteLeave(to, from, next) {
      console.log('',to.fullPath,from.fullPath)
      if(!to.query.allow){
        if (to.fullPath === '/home') {
          next();
        } else {
          console.log('')
          next('/home')
        }
      }else{
        next();
      }

      // 
      //  `this`
    }
  }
</script>

<style lang="scss" scoped>
  @import "../common/scss/mixin";

  .header {
    height: 453px;
    @include bk('result/cloud_02.jpg', 100);
    & p {
      color: #fff;
      font-size: 48px; /*px*/
      text-align: center;
      & b {
        line-height: 109px;
        font-size: 72px; /*px*/
      }
    }
  }

  #rador {
    height: 800px;
  }

  .chart-result {
    text-align: center;
    padding-bottom: 200px;
    padding-top: 100px;
    font-size: 18px; /*px*/
  }

  .suggests {
    padding: 0 30px;
  }

  .sug-title {
    font-size: 36px; /*px*/
    color: #4bc6ff;
    text-align: center;
    border-bottom: 2px solid #ceced4;
    padding-bottom: 44px;
  }

  .sug-item section .txt {
    padding-top: 52px;
    font-size: 22px; /*px*/
    line-height: 60px;
    color: #666;
    min-height: 360px;
  }

  .per {
    font-size: 30px; /*px*/
    color: #4bc6ff;
    text-align: right;
  }

  .per-ti {

    padding-top: 40px;
  }

  .me-lst-li {
    margin-top: 37px;
  }

  .me-lst-li p {
    color: #999;
    font-size: 30px; /*px*/
    overflow: hidden;
    & .per {
      float: right;
    }
  }

  .bar-in {
    display: inline-block;
    width: 0%;
    height: 100%;
    background-color: #4bc6ff;
    border-radius: 19px;
    font-size: 20px;
  }

  .bar-out {
    height: 19px;
    background-color: #f9f9f9;
    -webkit-border-radius: 100px;
    margin-bottom: 21px;
  }

  .user-num {
    margin-top: 188px;
    font-size: 30px; /*px*/
    color: #999;
    text-align: center;
    line-height: 43px;
    & span {
      font-size: 43px; /*px*/
      color: #4bc6ff;
    }
  }

  .code {
    & img {
      width: 300px;
    }
    height: 553px;
  }

  .re-test {
    text-align: right;
    border-bottom: 2px solid #ceced4;
    padding-bottom: 33px;
    & span {
      font-size: 30px;
      color: #4bc6ff;
      padding-right: 37px;
    }
  }

  .footer {
    padding: 0 30px;
  }

  .isHelp {

    padding-top: 20px;
    padding-bottom: 200px;
    & span {
      display: inline-block;
      width: 474px;
      font-size: 30px;
      color: #999;
    }
    & input {
      width: 68px;
      height: 62px;
      border: 1px solid #b8ecff; /*no*/
      background-color: #fff;
      color: #4bc6ff;
      font-size: 18px; /*px*/

    }
    & input:nth-of-type(2) {
      margin-left: 47px;
    }
    & .iptActive {
      background-color: #4bc6ff;
      color: #fff;
    }
  }

  .echarts {
    width: 100%;
    height: 900px;
  }
  .syn-improve{
    padding-top:20px;
    font-size:28px;/*px*/
    line-height:50px;
  }
</style>



